import { VerifyEmailComponent } from './auths/verify-email/verify-email.component';
import { ForgotPasswordComponent } from './auths/forgot-password/forgot-password.component';
import { PageNotFoundComponent } from './employees/page-not-found/page-not-found.component';
import { EmployeelistComponent } from './employees/employeelist/employeelist.component';
import { CreateComponent } from './employees/create/create.component';
import { LoginComponent } from './auths/login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule, PreloadAllModules } from '@angular/router';
import { RegisterComponent } from './auths/register/register.component';






const routes: Routes = [
  { path: '',  redirectTo: 'login', pathMatch: 'full'},
  { path: 'login', component: LoginComponent},
  { path: 'register', component: RegisterComponent},
  { path: 'forgot-password', component: ForgotPasswordComponent},
  { path: 'verify-email', component: VerifyEmailComponent},
 // { path: 'dashboard', component: DashboardComponent, loadChildren: 'employees/employee.module#Employee.Module'},

    // wildcard Route
 { path: '**', component: PageNotFoundComponent}

];

@NgModule({
  imports: [RouterModule.forRoot(routes, {preloadingStrategy: PreloadAllModules, // enableTracing:true
  })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
