import { EmployeeService } from './employee.service';
import { Injectable } from '@angular/core';
import { CanActivate, RouterStateSnapshot, ActivatedRouteSnapshot, Router, CanActivateChild } from '@angular/router';


@Injectable({
  providedIn: 'root'
})
export class CanActivateGuardService implements CanActivate{
  constructor(private _employeeService: EmployeeService, private _router: Router) { 

  }
  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot):boolean{
     let employeeExist = this._employeeService.getEmployee(+route.paramMap.get('id'));
     
     if(employeeExist){
         return true;
     } else {   
       this._router.navigate(['dashboard/notfound']);
       return false;
       
     }
   }

  
}
