 import { Router, CanActivate } from '@angular/router';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore } from '@angular/fire/firestore';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AuthServiceService {

  newUser: any;
  // passing Error message

  //emit happen when the BehaviorSubject is private and other is public
  private eventAuthError = new BehaviorSubject<string>('');

  // public
  eventError$ = this.eventAuthError.asObservable();
  showSuccessCreated: boolean;
 
  constructor( private authz: AngularFireAuth, private db: AngularFirestore, private route: Router) 
  { }

  


  // geting user details

  getUserState(){
   return this.authz.authState;
  }

  // LoggIn Users
  login(email: string , password: string ) {
    this.authz.auth.signInWithEmailAndPassword(email, password)
    .catch( error => {
      this.eventAuthError.next(error);
  }).then(userCredential => {
    if (userCredential) {
      this.route.navigate(['/dashboard']);
    }
  });
  }
  
  // Creating User
  createUsers(user) {
    this.authz.auth.createUserWithEmailAndPassword(user.email, user.passwordGroup.password).then(userCredential => {
       this.newUser = user;
       console.log(userCredential);

       userCredential.user.updateProfile({
         displayName: user.firstName + '  ' + user.lastName
       });

       this.insertUserData(userCredential).then( () => {
        setTimeout(() => this.showSuccessCreated = false, 3000);
        this.route.navigate(['/login']);
      });

    }).catch( error => {
      this.eventAuthError.next(error);
  });
  }
  insertUserData(userCredential: firebase.auth.UserCredential) {
    return this.db.doc(`Users/${userCredential.user.uid}`).set({
      email: this.newUser.email,
      firstname: this.newUser.firstName,
      lastname: this.newUser.lastName,
      role: 'user'
    });
  }
 logout() {
   return this.authz.auth.signOut();
 }
 
}
