import { EmployeeService } from './../employee.service';
import { IEmployee } from './../model/iemployee';
import { Component, OnInit, ChangeDetectionStrategy, Input } from '@angular/core';
import { Router, ActivatedRoute, ParamMap} from '@angular/router';


@Component({
  selector: 'app-employeelist',
  templateUrl: './employeelist.component.html',
  styleUrls: ['./employeelist.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class EmployeelistComponent implements OnInit {

  employees: IEmployee[];
  employee: IEmployee;
  public errorMessage;
  selectId: number;
  employeeSearch: string;
  date: number = Date.now();
  @Input('data') myemployee: IEmployee[] = [];
  page: number = 1;

 
  constructor(private _employeelist: EmployeeService, private _route: Router, private _activeRoute : ActivatedRoute)
   { 

    this.employees = this._activeRoute.snapshot.data['employeeList'];
    setInterval(()=>{
      this.date = Date.now();
    }, 1)

  }

 


  ngOnInit() {

    //subsribe to Observable
   //  this._employeelist.getEmployees().subscribe(
   //   (listEmployee)=>this.employees = listEmployee,
    //  (error)=>this.errorMessage = error
   // ); 
    
    this._activeRoute.paramMap.subscribe((params : ParamMap)=>{
      let id = parseInt(params.get('id'));
      this.selectId = id;
    });    
  }
 

 
  isSelected(detail){
    return detail.id === this.selectId;
  }

  deleteEmployeeId(employee) {
    this._employeelist.deleteEmployee(employee.id).subscribe(
   () => alert(`Employee with ID ${employee.id}`),
   (err) => console.log(err)

);

}
 

  //navigate to employee edit route
  editEmployee(employeeId:number){
    this._route.navigate(['/dashboard/edit', employeeId ])

  };
  createEmployee(){
    this._route.navigate(['/dashboard/create'])
  };

  
  employeeDetail(detailId:number){
     this._route.navigate(['/dashboard/detail', detailId])
  };

}
