import { Directive} from '@angular/core';
import { NG_VALIDATORS, AbstractControl } from '@angular/forms';

@Directive({
  selector: '[appPasswordconfirmvalidation]',
  providers:[
    {
      provide: NG_VALIDATORS,
      useExisting:PasswordconfirmvalidationDirective,
      multi: true,
    
    }
  ]
})
export class PasswordconfirmvalidationDirective {
  validate(passwordGroup:AbstractControl): {[key: string] : any} | null{
    let Password = passwordGroup.get('password');
    let ConfirmPassword = passwordGroup.get('confirmPassword');

    if( Password && ConfirmPassword && Password.value !== ConfirmPassword.value){
      return {'notMatch' : true}
    }
    return null;

  }

  constructor() { }

}
