import { EmployeeService } from './employee.service';
import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { IEmployee } from './model/iemployee';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})

// resolve Guard is use in a scenerio where by navigating to another we have to check of there is data or not in that route
export class ResolverGuardService implements Resolve<IEmployee[]>{
  employee: IEmployee[];
 
  
  constructor(private _employee: EmployeeService) { }
   resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<IEmployee[]>{
     return this._employee.getEmployees();
   }


  
}
