import { AttendanceComponent } from './attendance/attendance.component';
import { HolidayComponent } from './holiday/holiday.component';
import { ResolverGuardService } from './resolver-guard.service';
import { CanDeactivateGuardService } from './can-deactivate-guard.service';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { EmployeelistComponent } from './employeelist/employeelist.component';
import { CreateComponent } from './create/create.component';


import { DashboardComponent } from '../dashboard/dashboard.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule, CanActivate } from '@angular/router';
import { EmployeeDetailComponent } from './employee-detail/employee-detail.component';
import { CanActivateGuardService } from './can-activate-guard.service';
import { AuthguardService } from '../auths/authguard.service';





const routes: Routes = [
  { path: 'dashboard', component: DashboardComponent, canActivate:[AuthguardService],
     children: [
      {path: '', redirectTo: 'employees', pathMatch: 'full'},
      {path: 'employees', component: EmployeelistComponent, resolve: {employeeList: ResolverGuardService}},
      {path: 'detail/:id', component: EmployeeDetailComponent,  canActivate: [CanActivateGuardService],
       resolve: {employeeList: ResolverGuardService} },
      {path: 'create', component: CreateComponent, canDeactivate: [CanDeactivateGuardService]},
      {path: 'edit/:id', component: CreateComponent},
      { path: 'schedule', component: HolidayComponent,  resolve: {employeeList: ResolverGuardService}},
      { path: 'attendance', component: AttendanceComponent,  resolve: {employeeList: ResolverGuardService}},
      { path: 'notfound', component: PageNotFoundComponent},

     ]
    },



];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EmployeeRoutingModule { }
