import { IEmployee } from './model/iemployee';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { HttpClient, HttpHeaders, HttpErrorResponse} from "@angular/common/http";
import { catchError, delay, map, retry} from "rxjs/operators";



@Injectable({
  providedIn: 'root'
})
export class EmployeeService {

baseUrl ='http://localhost:3000/employee'

//baseUrl ="https://hrmsapp-59e8f.firebaseio.com/employees/yE1YO08HWMJZK4wJk19f.json";

  constructor( private _httpClient: HttpClient) { }



  // Retrieving All Employees using GET method
  getEmployees(): Observable<IEmployee[]>{
    return this._httpClient.get<IEmployee[]>(this.baseUrl)
                          .pipe(retry(1),catchError(this.errorHandler), delay(1000));
   }

   errorHandler(error:HttpErrorResponse){
     return throwError('Cannot connect to database, Please check your network connection or try again later!')

   }
   // Retrieving specific Employee using GET method
   getEmployee(id:number): Observable<IEmployee>{
    return this._httpClient.get<IEmployee>(`${this.baseUrl}/${id}`)
                     .pipe(retry(1),catchError(this.errorHandler));
   }

   // Creating A new Employee using POST method
   addNewEmployee(employee:IEmployee): Observable<IEmployee>{
    return this._httpClient.post<IEmployee>(this.baseUrl, employee,{
      // this the content-Type that notify the server the type of data we are posti
      headers: new HttpHeaders({
         'Content-Type' : 'application/json'
       })
    }).pipe(retry(1),catchError(this.errorHandler));
   }

     // Updating existing Employee using PUT method
   updateEmployee(employee:IEmployee): Observable<void>{
    return this._httpClient.put<void>(`${this.baseUrl}/${employee.id}`, employee,{
       headers: new HttpHeaders({
         'Content-Type' : 'application/json'
       })
    }).pipe(retry(1),catchError(this.errorHandler));
   }

    // Deleting existing Employee using Deleting method
     deleteEmployee(id:number): Observable<any>{
     return this._httpClient.delete(`${this.baseUrl}/${id}`)
                    .pipe(retry(1),catchError(this.errorHandler));
     }
}

