import { AuthModel } from './../model/auth-model';
import { AuthServiceService } from './../auth-service.service';
import { Component, OnInit, HostBinding} from '@angular/core';
import { Router, NavigationStart, NavigationEnd, Event, NavigationCancel, NavigationError} from '@angular/router'




@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  authError: any;
  changeText = 'Sign In'
  public showLoading:boolean;

  constructor( private auth: AuthServiceService, private _route: Router) {
    
    this._route.events.subscribe((routerEvent: Event)=>{
      if (routerEvent instanceof NavigationStart){
        this.showLoading =true;
      }
      if (routerEvent instanceof NavigationEnd){
        this.showLoading =false;
      }
      if (routerEvent instanceof NavigationCancel){
        this.showLoading =false;
      }
      if (routerEvent instanceof NavigationError){
        this.showLoading =false;
      }
  
    });
   }

  ngOnInit() {
    this.auth.eventError$.subscribe(error => this.authError = error)       
  }
  loginUser(frm){
    if(this.authError){
      this.changeText = 'Sign In';
    }else{
      this.changeText = 'Please Wait...';
    }
   
    this.auth.login(frm.value.email, frm.value.password);
  }

 

  

}
